import React, { useState } from "react";
import "./App.css";
import FilmsCards from "./features/componet/filmsCard/FilmsCards";
import NavBar from "./features/componet/navbar/NavBar";
import {
  Redirect,
  Route,
  BrowserRouter as Router,
  useHistory,
  Switch,
  NavLink,
} from "react-router-dom";
import { Home } from "./features/componet/pages/home/Home";
import Footer from "./features/componet/footer/Footer";
import About from "./features/componet/pages/about/About";
import Series from "./features/componet/pages/series/Series";
import Login from "./features/componet/login/Login";
import DetailFilm from "./features/componet/pages/detailFilm/DetailFilm";

function App() {
  const [Auth, setAuth] = useState(true);
  const history = useHistory();
  return (
    <>
      <div className="container">
        <header className="App-header"></header>
        <Router>
          <NavBar />
          <Switch>
            <Route exact path={"/"} component={Home} />
            <Route exact path={"/series"} component={Series} />
            <Route exact path={"/about"} component={About} />
            <Route exact path={"/login"} component={Login} />
            <Route exact path={"/title/:title"} component={DetailFilm} />
            {Auth ? (
              <Route exact path={"/films"} component={FilmsCards} />
            ) : (
              <Redirect from="*" to="/login"></Redirect>
            )}
          </Switch>
        </Router>

        <footer className="App-footer">
          <Footer />
        </footer>
      </div>
    </>
  );
}

export default App;
