import Button from "react-bootstrap/Button";
import CardBootstap from "react-bootstrap/Card";
import "./films.css";
import {Link} from "react-router-dom";
import { useAppSelector } from "../../../app/hooks";
import {selectArray,selectLoading,selectSearch} from "../../slice/searchSlice";
import { useSelector } from "react-redux";
import { useEffect, useState } from "react";
import DetailFilm from "../pages/detailFilm/DetailFilm";

function FilmsCards() {
  const randomNumber = Math.floor(Math.random() * (3 - 0) + 1);

  const ClassAnimate = [
    "animate__backInDown",
    "animate__backInRight",
    "animate__backInRight",
    "animate__backInLeft",
  ];
  const ListClassAnimate = ["animate__animated", ClassAnimate[randomNumber]];
  const searchInput = useAppSelector(selectSearch);
  const films = useSelector(selectArray);
  const loading = useSelector(selectLoading);
  const filterFilms:any[] = films.filter((film: any) =>
    film.Title.toLowerCase().includes(searchInput.toLowerCase())
  );
  const [filmDetails, setfilmDetails] = useState({});

  function DetailsHandler(selectFilm: any) {
    console.log(selectFilm.Title);
    
    <Link to={`/title/${selectFilm.Title}`}/>
  }
  return (
    <>
      <div className="container-items">
        {loading && <h3 className="loading-text">CARICAMENTO IN CORSO...</h3>}
        {filterFilms.map((film: any) => (
          <CardBootstap
            className={`container-card ${ListClassAnimate.join(" ")}`}
            key={film.Title}
          >
            <CardBootstap.Img
              className="card-img"
              variant="top animate__animated animate__backInRight"
              src={film.Poster}
            />
            <CardBootstap.Body>
              <CardBootstap.Title className="img-title animate__animated animate__backInRight">
                <strong>Title: </strong>
                {film.Title}
              </CardBootstap.Title>
              <CardBootstap.Text className="animate__animated animate__backInLeft">
                Yeard: {film.Year} - Runtime: {film.Runtime}
                <br />
                <strong>Description</strong>
                <br />
                Some quick example text to build on the card title and make up
                the bulk of the card's content.
              </CardBootstap.Text>
             <Link className="btn btn-primary" type="button"   to={`/title/${film.Title}`}>Dettagli</Link>
            </CardBootstap.Body>
          </CardBootstap>
        ))}
      </div>
    </>
  );
}

export default FilmsCards;
