import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";
import axios from "axios";

export const searchSlices = createSlice({
  name: "search",
  initialState: {
    input: "",
    filmArray: [],
    loading: false,
    detailFilm:{}
  },
  reducers: {
    inputChanged(state, action) {
      state.input = action.payload.toLowerCase();
    },inputChanged1(state, action) {
      state.input = action.payload.toLowerCase();
    }
   
  },
  extraReducers: (Builder) => {
    Builder.addCase(fetchData.fulfilled, (state, action) => {
      state.filmArray = action.payload;
      //console.log(state.filmArray);
      state.loading = false;
    });
    Builder.addCase(fetchData.pending, (state, action) => {
      state.loading = true;
    });
  },
});
export const fetchData:any = createAsyncThunk(
  "data/fetchData",
  async (url: string) => {
    const response = await axios.get(url);
    return response.data;
  }
);

export const { inputChanged } = searchSlices.actions;
export const selectSearch = (state: RootState) => state.searchInput.input;
export const selectArray = (state: RootState) => state.searchInput.filmArray;
export const selectLoading = (state: RootState) => state.searchInput.loading;
export const selectdetailFilm = (state: RootState) => state.searchInput.detailFilm;
export default searchSlices.reducer;
